import React from "react";
import QueueAnim from "rc-queue-anim";
import Banner from "../../assets/min/solutionimg.jpg";
// import Texty from "rc-texty";
import LogoWeiots from "../../assets/min/weiots-transparent.png";
import Img from "react-cool-img";
import WeLoading from "../../components/loading/Loading";
import { Helmet } from "react-helmet";

const Section2 = React.lazy(() => import("../../components/section/section2"));

const Solution = () => {
  return (
    <>
      <Helmet>
        <title>IKI provide end to end IoT solution | we-iots</title>
        <meta
          name="description"
          content="Our various solutions: energy monitoring solution, smart industry 4.0 solution, smart street light solution & smart environment."
        />
        <meta
          name="keywords"
          content="Energy monitoring solution, Smart industry 4.0 solution, Smart street light solution & Smart environment."
        />
      </Helmet>
      <QueueAnim>
        <QueueAnim
          key="a"
          style={{
            height: "720px",
            overflow: "hidden",
            maxHeight: "720px",
            position: "relative",
          }}
        >
          <div
            key="1"
            style={{
              backgroundImage: `url(${Banner})`,
              backgroundRepeat: "no-repeat",
              backgroundSize: "cover",
              backgroundPosition: "center",
              zIndex: 1,
              width: "100%",
              height: "100%",
              position: "absolute",
            }}
          ></div>
          <div
            key="2"
            style={{
              background: "rgba(0,0,0, 0.5)",
              position: "absolute",
              width: "100%",
              height: "100%",
              zIndex: 2,
            }}
          ></div>

          <div
            key="3"
            style={{
              position: "absolute",
              width: "100%",
              height: "100%",
              zIndex: 3,
              display: "flex",
              justifyContent: "center",
              alignItems: "center",
            }}
          >
            <span style={{ textAlign: "center" }}>
              <Img
                src={LogoWeiots}
                style={{
                  position: "relative",
                  display: "block",
                  width: "240px",
                  marginLeft: "auto",
                  marginRight: "auto",
                  left: 0,
                  right: 0,
                  textAlign: "center",
                  marginBottom: "24px",
                  // backgroundColor: "grey",
                }}
                alt="logo weiots"
              />

              <p
                style={{ color: "#fff", fontSize: "18px", lineHeight: "24px" }}
              >
                WE Provide End to End IOT Solution
              </p>
            </span>
          </div>
        </QueueAnim>
        <React.Suspense fallback={<WeLoading />}>
          <Section2 key="b" />
        </React.Suspense>
      </QueueAnim>
    </>
  );
};

export default Solution;
